import React from 'react';
import '../../styles/styles.css'

function MXenesR() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default MXenesR;

function MainContents() {
	const title = '  ';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>
			<h3>10. Ti3C2 MXenes</h3>
			<ol>
				<li><a href="https://pubs.rsc.org/en/content/articlelanding/2021/ma/d0ma00938e" target='_blank' rel="">Applications of MXene (Ti3C2Tx) in photocatalysis: a review</a></li>
				<li><a href="https://pubs.acs.org/doi/10.1021/acsanm.0c02481" target='_blank' rel="">Photocatalytic Applications of Two-Dimensional Ti3C2 MXenes: A Review</a></li>
				<li><a href="https://www.sciencedirect.com/science/article/pii/S0025540819317817?via%3Dihub" target='_blank' rel="">   RhB   Ti 3 C 2 OH/Bi 2 WO 6     </a></li>
				<li><a href="https://pubs.acs.org/doi/10.1021/acs.iecr.9b03680" target='_blank' rel="">MoS 2 @Ti 3 C 2        </a></li>
				<li><a href="https://www.sciencedirect.com/science/article/pii/S0013935120303029?via%3Dihub" target='_blank' rel="">       Bi 3 TaO 7 /Ti 3 C 2  </a></li>
				<li><a href="https://www.sciencedirect.com/science/article/pii/S0045653519330012?via%3Dihub" target='_blank' rel="">2D/2D Ti 3 C 2 / gC 3 N 4             </a></li>
				<li><a href="https://pubs.rsc.org/en/content/articlelanding/2019/EN/C9EN00567F"> target='_blank' rel=""2D  CdS@Ti 3 C 2 @     MXene Z-scheme      TiO 2 </a></li>
				<li><a href="https://www.sciencedirect.com/science/article/pii/S138589472030913X?via%3Dihub" target='_blank' rel="">MXene Ti 3 C 2    Z           TiO 2 /gC 3 N 4 </a></li>
				<li><a href="https://nanoscalereslett.springeropen.com/articles/10.1186/s11671-020-03314-z" target='_blank' rel="">    R-scheme Ti 3 C 2 MXene/MoS 2  2D/2D </a></li>

			</ol>

		</div>
	)
}