import React from 'react';
import '../../styles/styles.css'

function PhotonAvalancheR() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default PhotonAvalancheR;

function MainContents() {
	const title = '  ';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>
			<h3>18.  (Photon avalanche)</h3>
			<ul>
				<li><a href="https://pubs.acs.org/doi/full/10.1021/acssuschemeng.7b02806?src=recsys" target='_blank' rel="">   NaYF 4 / SnO 2 / Ag    UV-Vis-NIR         </a></li>
				<li><a href="https://pubs.rsc.org/en/content/articlelanding/2014/CP/C3CP53847H#divAbstract" target='_blank' rel="">Yb  (Tm, Ho)   KLu (WO 4 ) 2      : Eu  </a></li>
							
			</ul>

		</div>
	)
}

