import React from 'react';
import '../../styles/styles.css'

function PhotothermalR() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default PhotothermalR;

function MainContents() {
	const title = '  ';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>
			<h3>16. (Photothermal-enhanced catalyst)</h3>
			<ul>

				<li><a href="https://pubs.rsc.org/en/content/articlelanding/2016/SC/C6SC03239G#!divAbstract" target='_blank' rel="">Cu7S4 microsphere@zeolitic</a></li>
				<li><a href="https://www.sciencedirect.com/science/article/pii/S246802571730064X" target='_blank' rel="">A review on photo-thermal catalytic conversion of carbon dioxide</a></li>
				<li><a href="https://chemistry-europe.onlinelibrary.wiley.com/doi/10.1002/cssc.201900175" target='_blank' rel="">  UV / Vis / IR </a></li>
				<li><a href="https://pubs.rsc.org/en/content/articlelanding/2018/TA/C8TA01362D#!divAbstract" target='_blank' rel="">Co3O4      ( )</a></li>
				<li><a href="https://pubs.rsc.org/en/content/articlelanding/2016/ta/c5ta07617j#!divAbstract" target='_blank' rel="">/     Co3O4 </a></li>
				<li><a href="https://www.sciencedirect.com/science/article/pii/S2211285519304860" target='_blank' rel="">Fe3Si assisted Co3O4</a></li>
				<li><a href="https://www.sciencedirect.com/science/article/pii/S0169433219310852" target='_blank' rel="">NiCo2O4-CuCo2O4-FeCo2O4-MnCo2O4</a></li>
				<li><a href="https://pubs.rsc.org/en/content/articlelanding/2020/sm/d0sm00306a#!divAbstract" target='_blank' rel="">        </a></li>
				<li><a href="https://pubs.acs.org/doi/10.1021/acs.nanolett.9b01417" target='_blank' rel="">     CrPS 4       </a></li>

			</ul>

		</div>
	)
}
